.faq{
    margin: 4rem auto;
    width: 100%;
    text-align: center;
    h2{
        font-size: 2rem;
    }
    &__accordion{
        width: 80%;
        padding: 0 2rem;
        margin: 2rem auto;
        text-align: left;
        &__title{

            ul{
                list-style: none;
            }
            padding: 1rem .75rem;
            background-color: #f1f1f1;
            color: #125082;
            border-radius: 15px;
            margin-top: .5rem;
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: space-between;
            p{
                font-weight: 600;
            }
        }
        &__text{
            padding: 1rem 2rem 1rem 0;
            display: none;
        }

    }
}

@media (max-width: 768px){
    .faq{
        width: 100%;
        padding: 0;
        h2{
            font-size: 1.4rem;
        }
        &__accordion{
            padding: 0 0;
            width: 100%;
            &__text{
                padding: 1rem 2rem 1rem -.5rem;
            }
        }
        
    }
}
.active{
    background-color: aqua !important;
}
