.howUseSection{
    width: 100%;
    margin: 1rem auto;
    display: flex;
    flex-direction: column;
    &__block{
        display: flex;
        width: 70%;
        height: 200px;
        flex-direction: row;
        grid-gap: 2rem;
        align-items: center;
        box-sizing: border-box;
        margin: 2rem auto;
        padding: 2rem 2rem;
        align-items: center;
        background-color: #f1f1f1;
        border-radius: 8px;
        box-shadow: 1px 2px 1px 0px rgb(226, 226, 226);

        &__img{
            svg{
                height: 100px;
                width: 100px;
            }
           
        }
        img{
            position: relative;
            bottom: 5rem;
            filter: drop-shadow(10px 10px 1px #bebebe);
            right: 2rem;
        }
        &__text{
            width: 100%;
            &__title{
                font-size: 1.3rem;
            }
            &__info{
               margin-top: -0.8rem;
            }
        }
    }
}

@media (max-width: 768px){
    .howUseSection{
        width: 100%;
        margin: 1rem auto;
        padding: 1rem 1rem;
        display: flex;
        flex-direction: column;
        &__block{
            text-align: center;
            display: flex;
            width: 100%;
            height: auto;
            flex-direction: column;
            grid-gap: 2rem;
            align-items: center;
            box-sizing: border-box;
            margin: 2rem auto;
            padding: 1rem 2rem;
            align-items: center;
            text-align: center;
            background-color: #f1f1f1;
            border-radius: 8px;
            box-shadow: 1px 2px 1px 0px rgb(226, 226, 226);

            &__img{
                svg{
                    height: 60px;
                    width: 60px;
                    margin-top: -1rem;
                }
               
            }


            img{
                position: static;
                filter: drop-shadow(5px 5px 1px #bebebe);
            }
            &__text{
                width: 100%;
                &__title{
                    font-size: 1.3rem;
                    margin-top: -1.5rem;
                }
                &__info{
                   margin-top: -1rem;
                }
            }
        }
    }
}
